/* Variables */

// Base color
$blue: #324157;
$light-blue: #3a71a8;
$red: #c03639;
$pink: #ef889d;
$green: #30b08f;
$tiffany: #acdde7;
$yellow: #ea8a00;
$panGreen: #30b08f;

// Sidebar
$sideBarWidth: 210px;
$subMenuBg: #d7d7d7;
$subMenuHover: #c7c7c7;
$subMenuActiveText: #585858;
$menuBg: #e7e7e7;
$menuText: #4a4a4a;
$menuActiveText: #f28907; // Also see settings.sidebarTextTheme

// Login page
$lightGray: #eee;
$darkGray: #889aa4;
$loginBg: #2d3a4b;
$loginCursorColor: #fff;

// The :export directive is the magic sauce for webpack
// https://mattferderer.com/use-sass-variables-in-typescript-and-javascript
:export {
  menuBg: #e7e7e7;
  menuText: #4a4a4a;
  menuActiveText: #f28907;
}
/* Mixins */
@mixin clearfix {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}










































































.box-center {
  margin: 0 auto;
  display: table;
}

.text-muted {
  color: #777;
}

.user-profile {
  .user-name {
    font-weight: bold;
  }

  .box-center {
    padding-top: 10px;
    &.user-icon {
      border: 2px solid;
      border-radius: 100px;
      padding: 15px;
      i {
        font-size: 40px;
      }
    }
  }

  .user-role {
    padding-top: 10px;
    font-weight: 400;
    font-size: 14px;
  }

  .box-social {
    padding-top: 30px;

    .el-table {
      border-top: 1px solid #dfe6ec;
    }
  }

  .user-follow {
    padding-top: 20px;
  }
}

.user-bio {
  margin-top: 20px;
  color: #606266;

  span {
    padding-left: 4px;
  }

  .user-bio-section {
    font-size: 14px;
    padding: 15px 0;

    .user-bio-section-header {
      border-bottom: 1px solid #dfe6ec;
      padding-bottom: 10px;
      margin-bottom: 10px;
      font-weight: bold;
    }
  }
}
